.woocommerceCheckoutPageWrap {

  display: block;
  margin: auto;
  width: 100%;
  max-width: 1450px;
  min-height: 500px;
  //background: red;

  .checkoutHeader {
    height: 313px;
    width: 100%;
    max-width: 1448px;
    display: inline-block;
    vertical-align: top;
    background: url("../img/chekout-background.png");
    background-repeat: no-repeat;
    background-size: cover;

    h3{
      text-align: center;
    }
  }

  .currentPage {
    font-size: 42px;
    line-height: 42px;
    padding-top: 60px;
    font-family: 'Amsterdam-Deco-One';
    font-family: 'Amsterdam-Deco-Two';
    font-family: 'Amsterdam';
    font-family: 'Roboto';
    color: $ammsterdam;
    text-align: center;
    display: inline-block;
    width: 100%;
  }

  h3{
    font-family: 'Gotham Pro';
    font-weight: bold;
    text-align: center;
    font-size: 24px;
    text-align: left;
    margin-top: 11px;
    padding-bottom: 18px;
    color: #262626;
  }

  .price {
    font-family: 'Gotham Pro';
    font-weight: 400;
    text-align: left;
    font-size: 16px;
    line-height: 13px;
  }

  .promoWrap{
    margin-top: 15px;

    label{
      color: #aa6e0d;
      font-size: 15px;
      font-weight: bold;
      font-family: 'Gotham Pro';
    }

    fieldset{
      border-bottom: 1px solid #6f6f6f;
      margin-bottom: 50px;

      input{
        line-height: 42px;
        height: 45px;
        font-size: 16px;
        font-family: 'PT Sans';
        background: transparent;
        border: none;

        ::-moz-placeholder{
          color: #262626;
          opacity: 0.33;
          font-size: 16px;
          line-height: 16px;
        }

        ::placeholder{
          color: #262626;
          opacity: 0.33;
          font-size: 16px;
          line-height: 16px;
        }
      }

    }

  }/*end of promowrap*/

  .bonus,
  .code{
    color: $black;
    width: 188px;
    height: 41px;
    line-height: 41px;
    text-decoration: none;
    text-align: center;
    border:1px solid #a2a2a2;
    display: inline-block;
    vertical-align: top;
    margin-top: 29px;
    margin-left: 28px;
    margin-top: 38px;

  }

  .bonus {
    vertical-align: top;
    margin-top: 34px;
    margin-left: 28px;
  }

  .dot {
    border-radius: 50px;
    width: 7px;
    height: 7px;
    display: block;
    background: $ammsterdam;
    display: block;
    margin: auto;
  }

  .navigation {
    margin: auto;
    display: block;
    width: 170px;

    width: 184px;
    width: 191px;
    //width:204px;
    height: 40px;
    background: url("../img/navigation.png");
    background-size: contain;

    .nav1,
    .nav2,
    .nav3 {
      display: inline-block;
      vertical-align: top;
      height: 55px;
      line-height: 55px;

      a {
        font-size: 43px;
        font-family: 'Amsterdam-Deco-One';
        font-family: 'Amm';
        font-family: 'Amsterdam';
        font-family: 'Roboto';
        color: #d7bfa5;
        opacity: 1;
        position: relative;
        padding:0px 21px 0px 15px;

        &:after {
          position: absolute;
          content: '';
          display: block;
          width: 7px;
          height: 31px;
        }

        &:hover {
          text-decoration:none ;
        }
      }

      .active {
        color: $ammsterdam!important;
      }
    }
  }/*end fo navigation*/

  .costumerData,
  .productList {
    display: block;
    margin: auto;
    vertical-align: top;
    max-width: 710px;
    background: $colorproductlist;
    min-height: 427px;
    margin-top: 30px;
    padding-bottom: 50px ;

    @media (min-width: $screen-md-min) {
      padding-bottom: 0;
    }

    .hereInner{
      display: block;
      margin: auto;
      width: 100%;
      max-width: 320px;
      outline: 0px solid;

      @media (min-width: $screen-md-min) {
        max-width: 450px;
      }
    }

    .title {
      color:$cart;
      font-size:16px ;
      font-weight: bold;
      font-family: 'Gotham Pro';
      line-height: 20px;
      text-align: left;
      padding-top: 64px;

      label {
        display: block;
        text-align: left;
        color:$cartlabel;
        font-size: 24px;
        font-weight: bold;
        font-family: 'Gotham Pro';
        line-height: 29.2px;
        padding-top: 10px;

        @media (min-width: $screen-md-min) {
          font-size: 34px;
          line-height: 39.2px;
        }
      }
    }
  }

  .costumerData {
    background: $bgcustomer;
    padding-bottom: 20px;

    .loginWoo {
      text-decoration: none;
      color: white;
      width:126px ;
      height: 39px;
      background: $ammsterdam;
      line-height: 39px;
      text-align: center;
      display: inline-block;
      vertical-align: top;
    }

    .loginWooLabel {
      line-height: 21px;
      display: inline-block;
      vertical-align: top;
      font-family: 'Gotham Pro';
      font-size: 12px;
      padding-right: 2%;
    }

    .title {
      padding-bottom: 24px;
    }

    .hereStartForm{
      margin-top: 10px;

      .error {
        display: none;
        font-size: 12px;
        font-family: 'Gotham Pro';
        color:#ea5656 ;
        font-weight: 400;
      }
    }

    .form {

      fieldset{
        margin-bottom: 17px;

        input {
          width: 100%;
          max-width: 460px;
          display: inline-block;
          height: 50px;
          line-height: 50px;
          background: transparent;
          border: none;
          border-bottom: 1px solid #6f6d6a;

        }

        ::-moz-placeholder{
          color: #262626;
          opacity: 0.33;
          font-size: 16px;
          line-height: 16px;
        }

        ::placeholder{
          color: #262626;
          opacity: 0.33;
          font-size: 16px;
          line-height: 16px;
        }
      }
    }

    .bottomOfForm {
      width: 90%;
      margin: auto;
      display: block;
      margin-top: 18px;

      .col-md-6 {
        padding: 0;
        padding-left: 10px;
      }

      fieldset{

        input{
          display: none;
        }

        label{
          display: inline-block;
          width: 20px;
          height: 20px;
          background: url("../img/ok.png");
          background-repeat: no-repeat;
          background-position: center center;
          position: absolute;
          left: -23px;
          top: 11px;
        }
      }
    }

    .hereInner {
      max-width: 477px;

      .row {
        .col-md-3 {
          margin-left: 0;
          padding-left: 0;
        }
      }

      .row {
        margin: 0;
      }
    }
  }/*end of custom costumerData*/

  .productSummary {
    padding: 0;
    margin: 0;
    padding-top: 30px;

    .produclLi {
      display: inline-block;
      vertical-align: top;
      width: 100%;
      border-top: 1px solid #e3e3e3;
      position: relative;

      margin-bottom: 12px;

      .img-resposive {
        float: left;
      }

      .name {
        display: inline-block;
        vertical-align: top;
        font-size: 16px;
        font-family: 'PT Sans';
        font-weight: 400;
        color: #262626;
        padding-top: 11px;
        position: relative;
        left: 17px;
        top: -2px;
        width: 68%;

        @media (min-width: $screen-md-min) {
          width: 72%;
        }
      }

      .data{
        font-family: 'Gotham Pro';
        font-size: 16px;
        font-weight: 400;
        display: inline-block;
        vertical-align: top;
        padding-top: 0px;
        width: 100%;
        position: relative;
        left :5px;

        @media (min-width: $screen-md-min) {
          display: block;
          left: 22px;
          top: -6px;
        }

        number {
          font-family: 'Gotham Pro';
          font-weight: 400;
        }
      }

      .attribute {
        font-family: 'Gotham Pro';
        font-size: 13.66px;
        font-weight:400;
        color: #888888;
        display: inline-block;
        width: 100%;
        position: relative;
        top: 0;
        left: 5px;

        @media (min-width: $screen-md-min) {
          position: relative;
          top: -5px;
          left: 22px;
        }

      }

      .bonus{
        display: inline-block;
        vertical-align: top;
        margin-top: 0;

        .sum {

          em {
            font-style: normal;

          }
          em,
          label,span {
            font-size: 16px;
            font-family: 'PT Sans';
            font-weight: 400;
            color: #888;
          }
        }
      }

      .sum {
        position: absolute;
        right: 27px;
        top: 13px;

        @media (min-width: $screen-md-min) {
          right: 20px;
          top: 13px;
        }
      }

      .cancel {
        position: absolute;
        right: 5px;
        top:19px;
        display: inline-block;
        width: 12px;
        height: 12px;
        background: url("../img/cancel.png");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;

        @media (min-width: $screen-md-min) {
          right: 0px;
        }
      }

      .row {
        margin: 0;
      }
     }/*End of produclLi*/

  }

  .endCost {
    padding-top: 19px;

    .produclLi {
      border: none;

      .sum {
        position: relative;
        top:0;
        right: 0;
      }

      .data {
        font-family: 'Gotham Pro';
        font-size: 16px;
        font-weight: 400;
        display: inline-block;
        vertical-align: top;
        padding-top: 0;
        position: relative;
        left: 0px;
        top: 5px;

        number {
          display: inline-block;
          vertical-align: top;
          position: relative;
          top:0px;
          left:0px;
        }
      }

      .bonus {
        position: relative;
        right: 0px;
        top: 0px;
        margin-left: 16px;
        width: 118px;
        border-radius: 4px;
        margin-top: 0;
        padding-top: 0;
        display: inline-block;
        vertical-align: top;
        height: 36px;
        line-height: 34px;

        .sum {
          position: relative;
          right: 00px;
          top: 0px;
        }
      }
    }
  }

  .totalWrap {

    @media (min-width: 1600px) {

    }

    h3 {
      font-family: 'Gotham Pro';
      font-weight: 700;

      font-size: 24px;
      text-align: left;
      margin-top: 4px;
      padding-bottom: 18px;
      color: #262626;
    }
  }

  #step2 {
    //display: none;
  }

  #step3{
    //display: none;
  }

  .fullWidth {
    width: 100%;
    max-width: 1900px;

    .hereInner {
      margin: 0;
      max-width: 570px;

      @media (min-width: $screen-md-min) {
        margin-left: 125px;
      }

      .title {
        padding-top: 55px;
      }

      .paymentsGateway {
        margin: 0;
        padding: 0;

        @media (min-width: $screen-md-min) {
          margin-top: 79px;
          margin-left: 24px;
        }

        li {
          list-style-type: none;
          padding-bottom: 14px;
          padding: 10px;

          span {
            font-size: 16px;
            font-family: 'PT Sans';
            font-weight: 400;
            color: #262626;
            line-height: 29px;
          }
        }

        .visa {

          span {
            position: relative;

            &:after {
              background-repeat: no-repeat;
              background-size: contain;
              background-position: center center;
              background: url("../img/visa.png");
              width: 103px;
              height: 23px;
              display: inline-block;
              vertical-align: top;

              content: '';
              position: absolute;
              left: 100%;
            }
          }
        }

        .yandex {

          span {
            position: relative;
            &:after {

              content: '';
              position: absolute;
              left: 100%;

              background-repeat: no-repeat;
              background-size: contain;
              background-position: center center;
              background: url("../img/yandex.png");
              width: 27px;
              height: 32px;
              display: inline-block;
              vertical-align: top;
            }
          }
        }

        .paypal {

            span {
              position: relative;
              &:after {

                content: '';
                position: absolute;
                left: 100%;

                background-repeat: no-repeat;
                background-size: contain;
                background-position: center center;
                background: url("../img/paypall.png");
                width: 32px;
                height: 25px;
                display: inline-block;
                vertical-align: top;
              }
            }
        }

        .qiwi {

            span {
            position: relative;

            &:after {
              background-repeat: no-repeat;
              background-size: contain;
              background-position: center center;
              background: url("../img/qiwi.png");
              width: 33px;
              height: 27px;
              display: inline-block;
              vertical-align: top;


              content: '';
              position: absolute;
              left: 100%;
            }
          }
        }
      }
    }
  }

}

.woocommerce-page img,
.img-responsive {
  display: inline-block;
  float: left;
  padding-right: 20px;
  padding-bottom: 20px;
}

.sliderWrap {

  .title {
    font-size: 38px;
    font-family: 'Gotham Pro';
    line-height: 45px;
    font-weight: bold;
    color:#262626;
    display: inline-block;
    width: 100%;
    text-align: center;
  }

  .sliderContainer{
    background: url("../img/slider.png");
    background-size: cover;
    width: 100%;
    max-width:1500px;
    display: inline-block;
    height: 100px;
    height: 620px;
  }

}
/*Step 2*/
.hereStartFormStep2{

  .formStep2 {

    ul {
      margin: 0;
      padding: 0;

      li{
        list-style-type: none;
        display: inline-block;
        width: 100%;
        height: 50px;
        padding-bottom: 74px;

        fieldset{
          display: inline-block;
          vertical-align: top;
          width: 100%;
          height: 45px;

          input{
            display: none;
          }

          label{
            display: inline-block;
            background: #f2eada;
            border: 1px solid #282828;
            border-radius: 50%;
            width: 23px;
            height: 23px;
            position: relative;
            top: -13px;
            left: 10px;
          }

          checked,
          .checked {
            &:after{
              content: '';
              left: 3px;
              top: 0px;
              width: 15px;
              height: 15px;
              position: relative;
              display: inline-block;
              background: #a86b06;
              border-radius: 50%;
            }
          }

          span{
            font-size: 16px;
            font-family: 'Gotham Pro';
            position: relative;
            left: 11px;
            display: inline-block;
            top: 8px;
            width: 71%;
            font-weight: 400;
            text-transform: uppercase;

            em{
              display: block;
              font-style: normal;
            }
          }

          position: relative;
          display: inline-block;
          width: 100%;

          .data{
            display: inline-block;
            width: 100%;
            vertical-align: top;
            position: absolute;
            top: 7px;
            right: 7px;
            text-align: right;

            em {
              font-style: normal;
            }
          }
        }/*end of fieldset*/
      }
    }
  }
}

.magazinWrap{
  margin-top: 30px;
  background: #f6f6f6;
  display: inline-block;
  width: 96%;
  min-height: 100px;
  margin: 0;

  @media (min-width: $screen-md-min) {
    margin: 37px 21px 10px 31px;
  }

  .productList {
    .hereInner {
      max-width: 287px;

      @media (min-width: $screen-md-min) {
        max-width: 487px;
      }

      .title{
        font-size: 34px;
        padding-top: 16px;

        strong {
          color:#a66a0c;
        }
      }
    }
  }

  .miniText{
    font-family: 'PT Sans';
    font-size: 16px;
    font-weight: 400;
    color:#262626;
    line-height: 26px;
    padding-top: 35px;
  }

  .lunchStep3{
    width: 273px;
    height: 57px;
    display: inline-block;
    background: #a66b0c;
    text-decoration: none;
    line-height: 57px;
    text-align: center;
    color: $white;
    margin-top: 46px;
  }

  .map {
    background: url("../img/map.png");
    background-size: contain;
    width: 100%;
    max-width: 556px;
    height: 418px;
    background-repeat: no-repeat;
    background-position: center center;
    margin-top: 58px;
    margin-bottom: 200px;
  }
}

.paymentsGateway {

  li{
    list-style-type: none;

    input{
      display: none;
    }

    label {
      width: 16px;
      height: 16px;
      border-radius: 50%;
      display: inline-block;
      background: $white;
      position: relative;
      border:1px solid #ccc;
    }

    .checked{

      &:after{
        width: 6px;
        height: 6px;
        position: absolute;
        display: block;
        content: '';
        top: 4px;
        left: 4px;
        background: $black;
        border-radius: 50%;
      }
    }
  }
}

.endorder{
  width: 273px;
  height: 57px;
  display: inline-block;
  background: #a66b0c;
  text-decoration: none;
  line-height: 57px;
  text-align: center;
  color: #fff;
  margin-top: 35px;
  margin-left: 30px;
  margin-bottom: 100px;
}

.clear {
  clear: both;
}

.carouselWrap {
  display: block;
  width: 90%;
  margin: auto;

  @media (min-width: $screen-md-min) {
    width: 100%;
  }

  .container {
   width: unset!important;
  }

  .sliderTitle {
    font-family: 'Gotham Pro';
    font-size: 38px;
    font-weight: 700;
    color: #262626;
    width: 100%;
    display: inline-block;
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;

    label {
      display: block;
      width: 25px;
      margin: auto;
      color: #aa6f0d;
      font-family: 'Amsterdam';
      font-family: 'Amsterdam-Deco-One';
    }

    .dot {
      border-radius: 50px;
      width: 7px;
      height: 7px;
      display: block;
      background: $ammsterdam;
      display: block;
      margin: auto;
    }
  }

  .carousel {
    margin-bottom: 0;
    padding: 0 40px 30px 40px;
  }

  /* The controlsy */

  .carousel-control {
    /*left: -12px;*/
    height: 40px;
    width: 40px;
    background: none repeat scroll 0 0 #222222;
    border: 4px solid #FFFFFF;
    border-radius: 23px 23px 23px 23px;
    margin-top: 190px;
  }
  .carousel-control.right {
    right: -12px;
  }

  /* The indicators */
  .carousel-indicators {
    right: 50%;
    top: auto;
    bottom: -10px;
    margin-right: -19px;
  }

  /* The colour of the indicators */
  .carousel-indicators li {
    background: #cecece;
  }

  .carousel-indicators .active {
    background: #428bca;
  }

  .priceCarousel {
    width: 300px;
    margin: auto;
    display: block;
    text-align: center;
    line-height: 26px;
  }

  .stars {
    background: url("../img/slider/stars.png");
    background-repeat: no-repeat;
    width: 117px;
    height: 24px;
    margin: 0 auto;
    display: block;
    padding-top: 20px;
  }

  .priceCurrent {
    font-weight: 700;
    font-family: 'Gotham Pro';
    font-size: 22px;
    line-height: 22px;
    display: inline-block;
    text-align: center;
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.nextstep {
  text-decoration: none;
  color: #fff;
  width: 126px;
  height: 39px;
  background: #aa6f0d;
  line-height: 39px;
  text-align: center;
  margin: auto;
  display: block;
  margin-top: 50px;
  margin-bottom: 50px;


  &:hover {
    text-decoration:none ;
    box-shadow: 1px 1px 1px 1px black;
  }
}

.displaynone {
  display: none;
}

.displayblock {
 display: block;
}